.b-hints {
	display: flex;
	align-items: center;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 34px;
		height: 34px;
		margin: 0 .3rem;
		@include border-radius(50%);
		&--background {
			background-color: $color-dark-lighter; } }
	&__item-icon {
		font-size: 16px;
		color: #ffffff;
		opacity: .6; } }
