body,
html {
	height: 100%;
	margin: 0; }
body {
	font: 14px/1.333 $font-default;
	letter-spacing: -0.27px;
	background-color: $color-dark;
	//color: $font
	//@media screen and (min-width: $xl)
	//	font-size: 18px


	* {
		box-sizing: border-box;
		outline: none;
		&:hover,
		&:focus {
			outline: none; } }

	&.modal-open {
		overflow: hidden; } }
a {
	text-decoration: none;
	color: #ffffff;
	@include transition(all, .4s, ease); }


img {
	max-width: 100%;
	vertical-align: middle; }

h1, h2, h3, h4, h5, h6 {
	margin-top: 0; }

p {
	font-size: 16px;
	margin-top: 0;
	margin-bottom: 15px; }

button, input, textarea, .e-input {
	@include transition(all, .4s, ease); }


input, textarea, .e-input {
	display: block;
	border: 2px solid transparent;
	//background-color: $grey-light
	padding: 13px 11px 11px;
	width: 100%;
	font-size: 16px;
	&.error, &:focus:required:invalid {
 }		//border-color: $input-error-border
	&:focus,
	&:required:valid {
		border-color: $accent; }
	@media screen and (min-width: $lg) {
		padding: 15px 11px 13px; } }

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {

	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
	padding: 0;
	width: 0;
	display: none; }

input[type="time"] {
	font-size: 13px;
	padding-right: 0; }
button {
	padding: 0;
	background-color: transparent;
	border: none;
	box-shadow: none; }
video {
	max-width: 100%; }
table {
	width: 100%; }

::selection {
	background-color: $accent;
	color: #fff;
	text-shadow: none; }

::placeholder {
	//font-family: $add-font
	letter-spacing: -0.27px;
 }	//color: $font-light


ul {
	list-style: none;
	padding: 0;
	margin: 0; }
