.b-navbar {
	margin-bottom: 2.1rem;
	&__item {
		display: block;
		padding: .95rem 2.5rem .95rem;
		color: #9c9d9e;
		&:hover,
		&:focus,
		&:active,
		&.active {
			background-color: $color-dark-lighter;
			color: #ffffff;
			.b-navbar__item-icon {
				opacity: 1; } } }
	&__item-icon {
		font-size: 14px;
		color: #ffffff;
		opacity: .6;
		margin-right: 1.9rem; } }



