.b-chat {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 58px;
	height: 58px;
	background-color: $color-dark-lighter;
	@include border-radius(3px);
	&__icon {
		font-size: 24px; }

	&__count {
		position: absolute;
		top: -.6rem;
		right: -.5rem;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 22px;
		height: 22px;
		background-color: $color-green;
		color: #ffffff;
		@include border-radius(50%); } }

