@import "../mixins/font-face";

@font-face {
	font-family: 'Roboto';
	src: local("Roboto Regular"), local("Roboto-Regular"), url('../assets/fonts/Roboto/Roboto-Regular.woff2') format("woff2"), url('../assets/fonts/Roboto/Roboto-Regular.woff') format("woff"), url('../assets/fonts/Roboto/Roboto-Regular.woff') format("truetype");
	font-weight: normal;
	font-style: normal; }

@font-face {
	font-family: 'Roboto';
	src: local("Roboto Light"), local("Roboto-Light"), url('../assets/fonts/Roboto/Roboto-Light.woff2') format("woff2"), url('../assets/fonts/Roboto/Roboto-Light.woff') format("woff"), url('../assets/fonts/Roboto/Roboto-Light.woff') format("truetype");
	font-weight: 300;
	font-style: normal; }


@font-face {
	font-family: 'Roboto';
	src: local("Roboto Medium"), local("Roboto-Medium"), url('../assets/fonts/Roboto/Roboto-Medium.woff2') format("woff2"), url('../assets/fonts/Roboto/Roboto-Medium.woff') format("woff"), url('../assets/fonts/Roboto/Roboto-Medium.woff') format("truetype");
	font-weight: 500;
	font-style: normal; }



