.b-select {
	position: relative;
	background-color: $color-dark-lighter;

	&__arrow {
		position: absolute;
		top: 50%;
		right: 1.25rem;
		@include translate(0, -50%); }
	&__arrow-icon {
		font-size: 12px;
		opacity: .5; }
	&__item {
		text-transform: uppercase;
		background-color: transparent;

		border: none;
		box-shadow: none;
		color: #ffffff;
		padding: 1rem 3.1rem 1rem 1.5rem;

		-moz-appearance: none;
		text-indent: 0.01px;
		text-overflow: '';
		-ms-appearance: none;
		appearance: none!important;

		@include border-radius(2px); } }


.b-select-text {
	position: relative;
	display: flex;
	align-items: center;
	&__arrow {
		order: 2;
		margin-left: .5rem;
		height: 21px; }

	&__arrow-icon {
		font-size: 10px; }


	&__item {
		text-transform: uppercase;
		background-color: transparent;
		border: none;
		box-shadow: none;
		color: #ffffff;
		-moz-appearance: none;
		text-indent: 0.01px;
		text-overflow: '';
		-ms-appearance: none;
		appearance: none!important; } }
