.b-sidebar {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	background-color: $color-sidebar-bg;
	&__header {
		position: relative;
		padding: 1.9rem 2.5rem 1.4rem;
		border-bottom: 1px solid rgba(255, 255, 255, 0.05);
		margin-bottom: 2.1rem; }
	&__body {
		padding-bottom: 4rem;
		display: flex;
		flex-direction: column;
		flex: 1; }
	&__chat {
		text-align: center;
		margin-top: auto; } }

